.navButtons{
    position: absolute;
    top: 73%;
    width: 40%;
    height: 25%;
    display: flex;
    justify-content: center;
}

.navButtons span{
    position: relative;
    width: fit-content;
    height: 100%;
    margin: 0 10px;
    display: inline-flex;
    align-items: center;
    font-size: 15px;
    color: #646464;
}

.navButtons span:hover{
    cursor: pointer;
    user-select: none;
    font-size: 20px;
    font-weight: bold;
    transition: .5s;
    padding: 0 5px;
    color: #a5a5a5;
}

a{
    text-decoration: none;
}

