.welcomePage{
    position: relative;
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.messageContainer{
    position: relative;
    width: 80%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    margin: 10px 0;
    border: 2px solid #313131;
}

.messageContainer span{
    margin: 10px;
    font-size: 15px;
    text-align: center;
    color: rgb(172, 137, 91);
}

.messageContainer i{
    font-weight: bold;
}

table{
    border-radius: 5px;
    /* border: 3px solid rgb(28, 170, 28); */
    box-shadow: 0 0 5px 2px rgb(165, 164, 164);
}

table, th, td{
    margin: 25px;
    padding: 5px;
    text-align: center;
    color: rgb(172, 137, 91);
}

.sideHeadings{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: right;
}

.sideMessage{
    padding: 5px;
    font-size: 13px;
    color: rgb(92, 91, 91);
}