.header{
    position: relative;
    width: 100%;
    height: 10em;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background: #313131;
  }

::-webkit-scrollbar{
  display: none;
}

.headline, .contact{
    position: absolute;
    width: 40%;
    height: 50%;
    margin: 1% 2%;
    border-radius: 5px;
    overflow-y: auto;
    display: flex;
    justify-content: center;
}

.headline{
  left: 0;
}

.contact{
  right: 0;
}

.text{
  font-weight: 500;
  color: #a5a5a5;
  /* color: rgb(92, 91, 91); */
}

.headline .text{
  position: absolute;
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 720px) {
  .contact{
    align-items: center;
  }
  br{
    display: none;
  }
}

@media screen and (max-width: 720px) {
  .rowIndex{
    position: absolute;
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
  }
  .rowIndex span{
    position: relative;
    width: 95%;
    min-height: 50px;
    align-items: center;
    justify-content: center;
  }

  .rowIndex span b{
    position: absolute;
    margin-bottom: 20px;
  }
  #br{
    display: none;
  }
}
.headline .text span{
  min-width: 120px;
  text-align: center;
  border-top: 1px solid #646464;
  border-bottom: 1px solid #646464;
}

.text span{
  margin: 5px;
  font-size: 12px;
  padding: 2px;
}

.rowIndex span{
  display: flex;
  flex-direction: row;
}