.myImage{
    position: relative;
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.myImageContainer{
    position: relative;
    width: 250px;
    height: 250px;
    margin: 20px 0;
    border: 1px solid #000;
}

#largeImage{
    border-radius: 0;
    cursor: text;
    opacity: 1;
}